<template>
  <div id="app">
    <div id="loading" v-if="$store.state.loading">
      <vue-loading type="spin" color="rgb(32, 160, 255)" :size="{ width: '40px', height: '40px' }"></vue-loading>
    </div>
    <div class="toolbar tabbar tabbar-labels" v-if="$store.state.isMenu">
      <div class="toolbar-inner">
        <router-link to="/home" class="tab-link">
          <!--<img src="./images/index_green.png">-->
          <i class="iconfont">&#xe637;</i>
          <span class="tabbar-label">首页</span>
        </router-link>
        <router-link to="/classification" class="tab-link">
          <!--<img src="./images/classification.png">-->
          <i class="iconfont">&#xe664;</i>
          <span class="tabbar-label">分类</span>
        </router-link>
        <router-link to="/shoppingCart" class="tab-link">
          <span class="cartNum" v-if="cartCount" v-text="cartCount"></span>
          <i class="iconfont">&#xe604;</i>
          <span class="tabbar-label">购物车</span>
        </router-link>
        <router-link to="/my" class="tab-link">
          <i class="iconfont">&#xe6d6;</i>
          <span class="tabbar-label">我的</span>
        </router-link>
      </div>
    </div>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script type="text/ecmascript-6">
  /* eslint-disable no-new */
  import vueLoading from 'vue-loading-template';
  import {mapState} from 'vuex';

  export default {
    data() {
      return {
      };
    },
    computed: {
      ...mapState({
        cartCount: (data) => {
          let cartCount = 0;
          if (!data.CartInfo.length && localStorage.getItem('CartInfo')) {
            data.CartInfo = JSON.parse(localStorage.getItem('CartInfo'));
          }
          data.CartInfo.forEach((val) => {
            cartCount += val.count;
          });
          return cartCount;
        }
      })
    },
    mounted() {
    },
    methods: {},
    components: {
      vueLoading
    }
  };

</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "../static/css/reset.css";
  @import "../static/css/framework7.ios.min.css";
  @import "../static/css/public.css";
  @import "../static/css/app.css";

  #app
    position: relative
    height: 100%
    width: 100%

  #loading
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(197, 197, 197, .9);
    left: 0;
    top: 0;
    z-index: 6666;
    display: flex;
    justify-content: center;

  .views
    z-index: 6000;

  .tabbar-labels
    position: absolute;
    background: #ffffff;
    font-size: 13px;
    width: 100%
    height: 50px
    backface-visibility: hidden;
    transform: translate3d(0, 0, 0);
    box-sizing: border-box;
    color: #929292;
    left: 0;
    bottom: 0;
    z-index: 5001;
    .toolbar-inner
      width: 100%;
      height: 100%;
      padding: 4px 0;
      overflow: hidden
      .tab-link
        padding-top: 1px;
        position: relative;
        .iconfont
          font-size: 20px;
        .cartNum
          min-width: 10px;
          height: 10px;
          line-height 10px;
          padding: 1px;
          text-align: center;
          border-radius: 10px;
          color: red;
          font-size: 10px;
          border: solid 1px red;
          background: #fff;
          position: absolute;
          left: 52%;
          top: 0;
      .active
        color: #66CC66

</style>




