<template>
  <div class="views">
    <div class="view view-main">
      <div class="navbar">
        <div class="navbar-inner navbar-on-center">
          <div class="left" @click="$router.back(-1)">
            <a href="javascript:" class="back link">
              <img src="../../images/back.png">
            </a>
          </div>
          <div class="center" style="left: -145.5px;">订单详情</div>
        </div>
      </div>
      <div class="pages navbar-through toolbar-through">
        <div data-page="placeOrder" class="page placeOrder page-on-center">
          <div class="toolbar tabbar tabbar-labels">
            <div class="toolbar-inner">
              <div class="column-top list-block media-list" style="margin:0;">
                <ul>
                  <li>
                    <div class="item-link item-content">
                      <div class="item-inner">
                        <div class="item-title-row">
                          <div class="item-title">
                          </div>
                          <div class="item-after">
                            <a href="javascript:" class="button" @click="cancelOrder(createOrderInfo.OrderId)">取消订单</a>&nbsp;&nbsp;
                            <a href="javascript:" class="button" @click="ContinuePay(createOrderInfo)">继续支付</a>
                          </div>
                        </div>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>
          <div class="page-content">
            <div class="list-block media-list" style="margin:15px 0;">
              <ul>
                <li>
                  <div class="item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">收货人：橘子</div>
                        <div class="item-after">18118725189</div>
                      </div>
                      <div class="item-text">上海市卢湾区南昌路203号202室</div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
            <div class="list-block media-list margin-content">
              <ul>
                <li v-for="OrderGoodsList in OrderGoodsInfos">
                  <a href="javascript:" class="item-content">
                    <div class="item-media">
                      <img src="../../images/11.jpg" width="60">
                    </div>
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title" v-text="OrderGoodsList.Name||OrderGoodsList.name2"></div>
                        <div class="item-after">
                          {{(OrderGoodsList.Price || OrderGoodsList.price) * OrderGoodsList.count | formatMoney}}
                        </div>
                      </div>
                      <div class="item-text" style="height: auto;">
                        {{'规格：'}}
                        <span v-if="OrderGoodsList.SubGoods.length"
                              v-for="OrderGoodsListSubGoods in OrderGoodsList.SubGoods"
                              v-text="`${OrderGoodsListSubGoods.Name} / `"></span>
                        <span v-if="!OrderGoodsList.SubGoods"
                              v-text="`${OrderGoodsList.UnitQuantity||OrderGoodsList.weight}/${OrderGoodsList.Unit||OrderGoodsList.unit}`"></span>
                      </div>
                      <div class="item-text">
                        {{'单价：'}}{{OrderGoodsList.Price || OrderGoodsList.price | formatMoney}}{{'/' + (OrderGoodsList.Unit || OrderGoodsList.unit)}}   {{'数量：' + OrderGoodsList.count + (OrderGoodsList.Unit || OrderGoodsList.unit)}}
                      </div>
                    </div>
                  </a>
                </li>
              </ul>
            </div>
            <div class="margin-content list-block media-list">
              <ul>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          商品小计
                        </div>
                        <div class="item-after" v-text="goodsSubtotal"></div>
                      </div>
                    </div>
                  </div>
                </li>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          实付款
                        </div>
                        <div class="item-after" v-text="goodsSubtotal"></div>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
            <div class="margin-none list-block media-list">
              <ul>
                <li>
                  <router-link to="/invoiceInformation/paperInvoice" replace class="item-link item-content"
                               style="padding-left:10px;">
                    <div class="item-inner" style="margin-left:10px;">
                      <div class="item-title-row">
                        <div class="item-title">发票</div>
                        <div class="item-after">明细（纸质）- 橘子</div>
                      </div>
                    </div>
                  </router-link>
                </li>
              </ul>
            </div>
            <div class="margin-content list-block media-list">
              <ul>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          下单时间
                        </div>
                        <div class="item-after" v-text="createOrderInfo.OrderTime">
                        </div>
                      </div>
                    </div>
                  </div>
                </li>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          订单编号
                        </div>
                        <div class="item-after" v-text="createOrderInfo.OrderId"></div>
                      </div>
                    </div>
                  </div>
                </li>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          支付方式
                        </div>
                        <div class="item-after">{{createOrderInfo.PayAmount | PayAmount}}</div>
                      </div>
                    </div>
                  </div>
                </li>
                <li>
                  <div class="item-link item-content">
                    <div class="item-inner">
                      <div class="item-title-row">
                        <div class="item-title">
                          交易流水号
                        </div>
                        <div class="item-after" v-text="createOrderInfo.Number"></div>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapState} from 'vuex';

  export default {
    data() {
      return {};
    },
    filters: {
      PayAmount: (val) => {
        let pay = {
          '0': '待付款',
          '2': '现金',
          '3': '银联支付',
          '4': '支付宝',
          '5': '微信支付'
        };
        return pay[val];
      },
      formatMoney: (val) => {
        if (Number(val)) {
          return '¥ ' + Number(val).toFixed(2);
        } else {
          return val;
        }
      }
    },
    activated() {
      this.$nextTick(() => {
        this.$store.commit('isLoading', false);
      });
    },
    computed: {
      ...mapState({
        'OrderGoodsInfos': state => state.CartInfo
      }),
      ...mapState({
        createOrderInfo: (data) => {
          if (!data.createOrderInfo.OrderId && localStorage.getItem('createOrderInfo')) {
            data.createOrderInfo = JSON.parse(localStorage.getItem('createOrderInfo'));
          }
          return data.createOrderInfo;
        },
        goodsSubtotal: (data) => {
          let TotalMoney = 0;
          data.CartInfo.forEach((val) => {
            if (val.isChecked) {
              TotalMoney += val.count * (val.price || val.Price);
            }
          });
          return '¥ ' + TotalMoney.toFixed(2);
        }
      })
    },
    methods: {
      cancelOrder(id) {
        this.axios({
          method: 'post',
          url: '/api/cashier/Cashier/CancelCashierOrder',
          data: {OrderId: id}
        }).then((res) => {
          res = res.data.Data;
//          this.$router.push('./');
        }).catch((err) => {
          console.log(err);
        });
      },
      ContinuePay(item) {
        this.axios({
          method: 'post',
          url: '/api/cashier/Cashier/CreateCashierOrderPayments',
          data: {OrderId: item.OrderId, PayType: item.PayAmount}
        }).then((res) => {
          res = res.data.Data;
          this.$router.push('./payment');
        }).catch((err) => {
          console.log(err);
        });
      }
    },
    created() {
    },
    mounted() {
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">

</style>



