<template>
  <transition name="move">
    <div class="specifications" :class="{'topmove':isMove}">
      <div style="position: relative;height:100%;" @touchmove.prevent2>
        <div class="goods">
          <img :src="goodsInfo.image||'/static/img/11.d0ae297.jpg'" alt="" width="60" height="60">
          <div class="goodsInfo">
            <div class="price" v-text="`¥ ${goodsInfo.price||goodsInfo.Price}`"></div>
            <div class="goodsId" v-text="`商品编号：${goodsInfo.goodsId||goodsInfo.Number}`"></div>
          </div>
          <i class="iconfont colseMask" @click="closeLayer">&#xe641;</i>
        </div>
        <div style="display:flex;height:69%;overflow: hidden;">
          <div ref="GoodsSpecifications" style="margin: 20px 0;">
            <ul>
              <li class="GoodsSpecifications" v-if="goodsInfo.IsPackage" v-for="GoodsPackageItem in GoodsPackage">
                <div class="title" v-text="GoodsPackageItem.Base.Name"></div>
                <div class="category">
                <span v-if="GoodsPackage.length" :class="{'active': SubGoodsItem.isActive}"
                      v-for="(SubGoodsItem, index) in GoodsPackageItem.SubGoods" v-text="SubGoodsItem.Name"
                      @click="isActives(GoodsPackageItem, SubGoodsItem)"></span>
                </div>
              </li>
              <li class="GoodsSpecifications" v-if="!goodsInfo.IsPackage">
                <div class="title">规格</div>
                <div class="category">
                <span class="active"
                      v-text="`${goodsInfo.weight||goodsInfo.Name} / ${(goodsInfo.UnitQuantity + goodsInfo.Unit)||goodsInfo.unit}`"></span>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="Determine" @click="DetermineSpecifications(goodsInfo, GoodsPackage)">确定</div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Bscroll from 'better-scroll';

  export default {
    props: ['goodsInfo', 'isMove'],
    data() {
      return {
        GoodsPackage: []
      };
    },
    methods: {
      closeLayer() {
        this.$emit('isClose', false);
      },
      DetermineSpecifications(item, GoodsPackage) {
        if (GoodsPackage.length) {
          item.SubGoods = [];
          GoodsPackage.forEach((val) => {
            val.SubGoods.forEach((value) => {
              if (value.isActive) {
                item.SubGoods.push(value);
              }
            });
          });
        }
        this.$emit('isClose', false);
      },
      isActives(item, item2) {
        this.$nextTick(() => {
          item.SubGoods.forEach((val) => {
            this.$set(val, 'isActive', false);
          });
          this.$set(item2, 'isActive', true);
        });
      },
      loadPackage() {
        if (this.goodsInfo.IsPackage) {
          this.axios.get('http://at.itsmore.com/cashier/Cashier/GetCashierGoodsById?goodsId=' + this.goodsInfo.GoodsId).then((res) => {
            res = res.data.Data;
            this.GoodsPackage = res.GoodsPackageGroups;
            this.GoodsPackage.forEach((val, index) => {
              if (!val.SubGoods.length) {
                this.GoodsPackage.splice(index, 1);
              }
            });
            this.$nextTick(() => {
              this.scroll = new Bscroll(this.$refs.GoodsSpecifications, {
                click: true
              });
//              this.scroll.refresh();
            });
          }).catch((err) => {
            console.log(err);
          });
        }
      }
    },
    created() {
      setTimeout(() => {
        this.loadPackage();
      }, 100);
    },
    activated() {
      setTimeout(() => {
        this.loadPackage();
      }, 100);
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .specifications
    position: fixed
    left: 0
    top: 100%
    bottom: 0
    z-index: 14000
    width: 100%;
    height: 70%;
    background: #fff;
    transform: translate3d(0, 0, 0);
    transition: all 0.1s linear;
    .goods
      display: flex;
      justify-content: flex-start;
      padding: 10px;
      border-bottom: solid 1px rgba(204, 204, 204, 0.3);
      .goodsInfo
        flex-grow: 1;
        padding-left: 10px;
        align-self: flex-end;
        .price
          color: #ff0000;
          font-size: 16px;
          padding-bottom: 2px;
        .goodsId
          font-size: 14px;
          color: #8e8e93;
      .colseMask
        padding: 2px 0 10px 15px;
        display: table;
    .GoodsSpecifications
      padding: 0 10px;
      .title
        margin: 5px 0 10px 0;
        color: #666;
      .category
        display: flex;
        flex-wrap: wrap;
        span
          border: solid 1px #ccc;
          border-radius: 3px;
          padding: 4px 8px;
          margin: 0 15px 10px 0;
          font-size: 12px;
        .active
          color: #ff0000;
          border: solid 1px rgba(255, 0, 0, .4);
    .Determine
      background: #ff4b52;
      color: #fff;
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 10px 0;
      text-align: center;
      font-size: 18px;
      letter-spacing: 10px;
</style>



