<template>
  <div v-if="CartListInfos.count">
    <div class="list-block media-list left-delete" style="margin:10px 0 0 0;">
      <ul>
        <li class="swipeout">
          <a href="javascript:" class="move swipeout-content item-content" ref="leftDel" @touchstart="_touchStart($event, CartListIndex)"
             @touchmove="_touchMove" @touchend="_touchEnd">
            <div class="item-media">
              <input type="checkbox" value="78" class="check" name="newslist" :id="`newslist-${CartListInfos._index}`">
              <label :for="`newslist-${CartListInfos._index}`" :class="{'check':CartListInfos.isChecked}"
                     @click="selectGoods(CartListInfos)"></label>
              <img src="../../images/11.jpg" width="90">
            </div>
            <router-link to="/goodsDetail" class="swipeout-content item-content">
              <div class="item-inner">
                <div class="item-title-row">
                  <div class="item-title" v-text="CartListInfos.name2||CartListInfos.Name"></div>
                </div>
                <div class="item-text" v-text="CartListInfos.title||CartListInfos.Comments"></div>
                <div class="cart-top">
                  <div class="commodity-money">
                    <span class="money price" v-show="CartListInfos.oldPrice"
                          v-text="`已降¥ ${CartListInfos.oldPrice}`"></span>
                  </div>
                  <div class="commodity-money">
                    <span class="money"
                          v-text="`¥ ${Number(CartListInfos.price||CartListInfos.Price).toFixed(2)}/${CartListInfos.unit||CartListInfos.Unit}`"></span>&nbsp;&nbsp;
                    <span class="unit" style="text-decoration:line-through">¥ 100.90/盒</span>
                  </div>
                </div>
              </div>
            </router-link>
          </a>
          <div class="swipeout-actions-right deleteIcon" ref="delIcon">
            <a href="javascript:" class="swipeout-delete" @click="goodsDel(CartListInfos)">删除</a>
          </div>
        </li>
      </ul>
    </div>
    <div class="column-top list-block media-list" style="margin:0;">
      <ul>
        <li>
          <div class="item-link item-content">
            <div class="item-inner">
              <div class="item-title-row">
                <div class="item-title commodity-money" style="font-weight:normal;">
                  <span class="price">小计：</span>
                  <span class="money"
                        v-text="`¥${Number(CartListInfos.count * CartListInfos.price||CartListInfos.count * CartListInfos.Price).toFixed(2)}`"></span>
                </div>
                <div class="item-after">
                  <span class="aCart" @click="changeNum(CartListInfos, '-1');">
                    <img src="../../images/minus.png" width="20">
                  </span>
                  <input class="numa" id="num1" type="text" ref="input" :value="CartListInfos.count"
                         @change="filtersVal(CartListInfos)">
                  <span class="aCart" @click="changeNum(CartListInfos, '1');">
                    <img src="../../images/add1.png" width="20">
                  </span>
                  <span v-text="CartListInfos.unit||CartListInfos.Unit" class="cartUnit"></span>
                </div>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
//  import Vue from 'vue';
  import {mapState} from 'vuex';

  export default {
    props: ['CartListInfos', 'CartListIndex'],
    data() {
      return {
        isChecks: this.isCheck,
        startX: 0,
        moveX: 0,
        disX: 0
      };
    },
    filters: {},
    mounted() {
      document.addEventListener('click', () => {
        this.delHide();
      }, false);
      document.addEventListener('touchstart', () => {
        this.delHide();
      }, false);
    },
    computed: mapState({}),
    methods: {
      changeNum(item, way) {
        if (way > 0) {
          item.count++;
        } else {
          item.count--;
          if (way < 0) {
            way = 0;
          }
        }
        this.$store.commit('ModifyQuantity', item);
      },
      selectGoods(item) {
        item.isChecked = !item.isChecked;
        this.$store.commit('selectGoods', item);
      },
      filtersVal(item) {
        if (Number(this.$refs.input.value) > 0) {
          item.count = Number(this.$refs.input.value);
          this.$store.commit('ModifyQuantity', item);
        }
      },
      _touchStart: function (ev, index) {
        ev = ev || event;
        this.delHide(index);
        if (ev.touches.length === 1) {
          if (!this.startX) {
            this.startX = ev.touches[0].clientX;
          }
        }
      },
      _touchMove: function (ev) {
        ev = ev || event;
        if (ev.touches.length === 1) {
//          ev.preventDefault();
          // 滑动过程中的实时位置
          this.moveX = ev.touches[0].clientX;
          // 滑动过程中实时计算滑动距离
          this.disX = this.startX - this.moveX;
          if (this.disX < 0 || this.disX < -200) {
            this.disX = 0;
            this.$refs.leftDel.style.left = this.disX + 'px';
            this.$refs.leftDel.style.transition = 'all 0.2s linear';
            this.$refs.delIcon.style.width = this.disX + 'px';
            this.$refs.delIcon.style.transform = 'translateX(100%)';
            this.$refs.delIcon.style.transition = 'all 0.2s linear';
          }
          if (this.disX > 0 && this.disX < 150) {
            this.$refs.leftDel.style.left = '-' + this.disX + 'px';
            this.$refs.leftDel.style.transition = 'all 0.2s linear';
            this.$refs.delIcon.style.width = this.disX + 'px';
            this.$refs.delIcon.style.transform = 'translateX(0)';
            this.$refs.delIcon.style.transition = 'all 0.2s linear';
          }
        }
      },
      _touchEnd: function (ev) {
        if (event.changedTouches.length === 1) {
          this.zIndex = 'z-index:" + -1 + "rem';
          //  手指移动结束后的水平位置
          let endX = event.changedTouches[0].clientX;
          //  触摸开始与结束,手指移动的距离
          this.disX = this.startX - endX;
          if (this.disX <= 60) {
            this.disX = 0;
            this.$refs.leftDel.style.left = this.disX + 'px';
            this.$refs.leftDel.style.transition = 'all 0.2s linear';
            this.$refs.delIcon.style.width = this.disX + 'px';
            this.$refs.delIcon.style.transform = 'translateX(100%)';
            this.$refs.delIcon.style.transition = 'all 0.2s linear';
          } else if (this.disX > 60) {
            this.disX = 110;
            this.$refs.leftDel.style.left = '-' + this.disX + 'px';
            this.$refs.leftDel.style.transition = 'all 0.2s linear';
            this.$refs.delIcon.style.width = this.disX + 'px';
            this.$refs.delIcon.style.transform = 'translateX(0)';
            this.$refs.delIcon.style.transition = 'all 0.2s linear';
          }
        }
      },
      delHide(index) {
        var len = document.getElementsByClassName('move').length;
        for (var i = 0; i < len; i++) {
          if (index === undefined) {
            document.getElementsByClassName('move')[i].style.left = '0px';
            document.getElementsByClassName('deleteIcon')[i].style.width = '0px';
            document.getElementsByClassName('deleteIcon')[i].style.transform = 'translateX(100%)';
          } else if (i !== index) {
            document.getElementsByClassName('move')[i].style.left = '0px';
            document.getElementsByClassName('move')[i].style.transition = 'all 0.2s linear';
            document.getElementsByClassName('deleteIcon')[i].style.width = '0px';
            document.getElementsByClassName('deleteIcon')[i].style.transform = 'translateX(100%)';
            document.getElementsByClassName('deleteIcon')[i].style.transition = 'all 0.2s linear';
          }
        }
      },
      goodsDel(item) {
        item.count = 0;
        this.$store.commit('ModifyQuantity', item);
        this.delHide();
      }
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .list-block .item-content
    justify-content: flex-start;
  /*.move
    transition: all 0.2s linear;
  .delMove
    left: 0;
    transition: all 0.2s linear;
    transform: 'translateX(100%)';*/
  .swipeout-delete
    width: 100%;
    display: flex;
    justify-content: center;
    white-space: nowrap;

  .aCart
    min-width: 20px;
    padding: 0 5px;
    display: inline-block;
    text-align center;

  #num1
    width: 30px;
    text-align: center;
    margin-top: -1px;

  .cartUnit
    display: inline-block;
    text-align center;
    margin: 2px 0 0 5px;
</style>



