<template>
  <div class="view view-main">
    <div class="navbar">
      <div class="navbar-inner navbar-on-center">
        <div class="left"></div>
        <div class="center" style="left: 10px;">分类</div>
        <div class="right">
          <a href="#">
            <img src="../../images/search.png">
          </a>
        </div>
      </div>
    </div>

    <div class="page-content classificationSelection" style="background: #fff;padding-top: 44px;">
      <img :src="converImage" class="width100">
      <div class="margin-content content-block index-column1">
        <div class="row">
          <router-link to="/order" v-for="(goodsClassItem, index) in goodsClassFilter" :key="index" class="item-link item-content col-33">
            <img :src="goodsClassItem.image">
            <div v-text="goodsClassItem.name">水果蔬菜</div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {},
    data() {
      return {
        converImage: '',
        goodsClass: []
      };
    },
    beforeRouteEnter (to, from, next) {
      next(vm => {
        vm.$store.commit('isLoading', true);
      });
    },
    created() {
      this.axios.get('/data/static/data.json').then((res) => {
        res = res.data;
        this.goodsClass = res.goodsClass;
        this.converImage = this.goodsClass[0].converImage;
        this.$store.commit('isLoading', false);
      }).catch(function (err) {
//        this.$store.commit('isLoading', false);
        console.log(err);
      });
    },
    activated() {
      this.$nextTick(() => {
        this.$store.commit('isLoading', false);
      });
    },
    computed: {
      goodsClassFilter: function () {
        return this.goodsClass.splice(2, this.goodsClass.length);
      }
    },
    mounted() {}
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">

</style>



