<template>
    <div class="views view-main">
      <div class="navbar">
        <div class="navbar-inner navbar-on-center">
          <div class="left" @click="$router.back(-1)">
            <a href="javascript:" class="back link">
              <img src="../../images/back.png">
            </a>
          </div>
          <div class="center" style="left: -145.5px;">全部订单</div>
        </div>
      </div>
      <div class="pages navbar-through toolbar-through">
        <div data-page="orderSettlement" class="page orderSettlement page-on-center">
          <div class="page-content">
            <div class="buttons-row">
              <a href="javascript:" class="button tab-link active">全部</a>
              <a href="javascript:" class="button tab-link">待付款</a>
              <a href="javascript:" class="button tab-link">待发货</a>
              <a href="javascript:" class="button tab-link">已发货</a>
              <a href="javascript:" class="button tab-link">已完成</a>
            </div>
            <orderStatus></orderStatus>
          </div>
        </div>
      </div>
    </div>
</template>

<script type="text/ecmascript-6">
  import orderStatus from './orderStatus.vue';

  export default {
    data() {
      return {};
    },
    beforeRouteLeave (to, from, next) {
      this.$store.commit('isMenu', true);
      next();
    },
    created() {
      this.$nextTick(() => {
        this.$store.commit('isLoading', false);
        this.$store.commit('isMenu', false);
      });
    },
    mounted() {
    },
    activated() {
    },
    methods: {
    },
    components: {
      orderStatus
    }
  };
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .views
    z-index: 6000;
</style>



